<template>
  <div v-if="tabList.length>1" class="good-detail-tab">
    <van-sticky @scroll="onTabScroll" offset-top=".9rem">
      <van-tabs
        v-model="activeTabIndex"
        line-height=".04rem"
        line-width=".46rem"
        :sticky="false"
        :animated="false"
        :swipeable="false"
        title-active-color="#333333"
        title-inactive-color="#333333"
        :swipe-threshold="6"
        background="white"
        @change="tabChange"
        color="#2878FF">
        <van-tab v-for="item of tabList" :title="item.label" :key="item.key" ></van-tab>
      </van-tabs>
    </van-sticky>
  </div>
</template>

<script>

const tabName = {
  detail: '商品介绍',
  specifications: '规格参数',
  aftersale: '安装售后'
}

export default {
  components: {},
  props: {
    tabs: Object
  },
  data () {
    return {
      tabList: [],
      activeTabIndex: 0
    }
  },
  created () {},
  mounted () {},
  beforeDestroy () {},
  methods: {
    tabChange(name, title) {
      this.$util.gioTrackWithVariable('MB15625')
    },
    onTabScroll (scrollInfo) {
      if (this.isTabFixed !== scrollInfo.isFixed) {
        this.isTabFixed = scrollInfo.isFixed;
        console.log('onTabScroll-isFixed：', scrollInfo)
      }
      this.$emit('scroll', scrollInfo)
    },
    switchTab () {
      this.$emit('switch', this.tabList[this.activeTabIndex] && this.tabList[this.activeTabIndex].key)
    }
  },
  computed: {},
  watch: {
    activeTabIndex: {
      handler (val) {
        if (this.tabList[val]) {
          this.switchTab()
        }
      },
      immediate: true
    },
    tabs: {
      handler (val) {
        this.tabList = []
        if (val) {
          for (const key of Object.keys(val)) {
            val[key] && this.tabList.push({ key: key, label: tabName[key] })
          }
          this.switchTab()
        }
      },
      immediate: true
    }
  }
}
</script>
